.itemName {
  padding: 10px;
  margin-right: 7px;
  font-size: 12px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 1.25px;
  text-transform: uppercase;
  text-decoration: none;

  font-family: -apple-system,
  BlinkMacSystemFont,
  'Segoe UI',
  Roboto,
  'Helvetica Neue',
  Arial,
  'Noto Sans',
  sans-serif,
  'Apple Color Emoji',
  'Segoe UI Emoji',
  'Segoe UI Symbol',
  'Noto Color Emoji';
}

.NavItem {
  display: flex;
  align-items: center;
  justify-content: space-around;

  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
  transition: background-color 200ms cubic-bezier(0.4, 0, 0.2, 1) 0s;
}

.iconWrapper {
  display: flex;
  margin: 0 8px;
}
.itemNameLight {
  composes:itemName;
  color: #010101;

}
.itemNameDark {
  composes: itemName;
  color: #f9f3f3;

}
.span {
  position: absolute;
  margin-left: 4px;
}
.NavItemActiveLight {
  color: #4b07e9e4;
}
.NavItemActiveDark {
  color: #8a5afae4;
}

.itemNameLight:hover,
.itemNameLight:focus {
  color: #4b07e9e4;
}

.itemNameDark:hover,
.itemNameDark:focus {
  color: #8a5afae4;
}